body {
    margin: 0;
    background-color: #eeeeee;
    font-family: 'Museo Sans Cyrl 300';
    color: #000;
}

nav {
    width: 100%;
    background-color: #ffffff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

nav ul {
    margin: 0;
}

nav ul li {
    display: inline-block;
}

nav ul li a {
    font-family: 'Museo Sans Cyrl 500';
    display: block;
    padding: 20px;
    text-decoration: none;
    color: #000;
    font-size: 18px;
    -webkit-transition: all .7s ease;
    -o-transition: all .7s ease;
    transition: all .7s ease;
}
nav ul li a:hover {
    color: #FE6B8B;
    background-color: #eeeeee;
}

::-moz-selection {
    background: #FE6B8B;
    color: #fff;
}

::selection {
    background: #FE6B8B;
    color: #fff;
}

.search-bar {
    margin-right: 60px;
    display: -ms-flexbox;
    display: flex;
}

.search-bar form {
    display: -ms-flexbox;
    display: flex;
}

.search-bar input{
    font-family: 'Museo Sans Cyrl 300';
    width: 200px;
    background: transparent;
    outline: none;
    border: none;
    border-bottom: 2px solid #000;
    color: #000;
    font-size: 18px;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.search-bar input:focus {
    width: 500px;
}

.search-bar button {
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'%3e%3cpath d='M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z'/%3e%3cpath d='M0 0h24v24H0z' fill='none'/%3e%3c/svg%3e ");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-color: transparent;
    outline: none;
    border: none;
    height: 30px;
    width: 30px;
}

.logo {
    position: relative;
    margin-bottom: -7px;
    height: 30px;
    width: auto;
    padding-right: 10px;
}

.movies-block {
    -ms-flex-pack: distribute;
        justify-content: space-around;
    display: -ms-flexbox;
    display: flex;
    padding-top: 30px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.movie {
    margin: 20px 0;
    width: 260px;
    background-color: #fff;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

p.movie-rating {
    position: absolute;
    background: -webkit-linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
    background: -o-linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
    background: linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
    top: 0;
    right: 0;
    color: #fff;
    padding: 5px 15px;
}

.movie-poster {
    display: block;
    position: relative;
    width: 260px;
    height: 360px;
    border-bottom: 5px solid #FE6B8B;
    overflow: hidden;
}

.movie-poster::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.2);
    opacity: 1;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
}

.movie:hover .movie-poster:before {
    opacity: 0;
}

a.movie-name {
    display: block;
    color: #000;
    text-decoration: none;
    margin: 10px 0;
    font-family: 'Museo Sans Cyrl 100';
}

.movie-info {
    padding: 10px;
}

.movie-poster img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

img.single-movie-poster {
    height: 500px;
    width: 350px;
    -o-object-fit: cover;
       object-fit: cover;
    border-bottom: 5px solid #FE6B8B;
}

.movie:hover {
    -webkit-box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.3);
            box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.3);
}

.movie:hover .movie-poster img {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}

.movie-genres {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.genre {
    display: block;
    color: #fff;
    text-decoration: none;
    font-size: 12px;
    padding: 2px 5px;
    margin: 5px;
    background: -webkit-linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
    background: -o-linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
    background: linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
}

.movie-page {
    padding: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
}

.movie-description {
    padding-left: 20px;
}

.movie-plot {
    color: #5c6c79;
}

ul.pagination {
    display: block;
    text-align: center;
}

ul.pagination li {
    display: inline-block;
}

ul.pagination li a {
    font-family: 'Museo Sans Cyrl 900';
    color: #fff;
    text-decoration: none;
    border: 15px solid #fff;
    border-top: 15px solid #fff;
    border-bottom: 15px solid #fff;
    padding: 5px 10px;
}

ul.pagination li a:first-child {
    border-left: none;
}

ul.pagination li.active a{
    background: -webkit-linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
    background: -o-linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
    background: linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%);
}


